import { addCustomClass, removeCustomClass } from "../functions/customFunctions";
import vars from "../_vars";
const {activeClass, range} = vars

range.map(function(item) {
  const slider = item.querySelector(".custom-range__range");
  const sliderCounter = item.querySelector(".custom-range__counter");
  const pointList = item.querySelector('.custom-range__wrapper-point');
  const pointItemFirst = pointList.querySelectorAll('li')[1];
  const pointItemSecond = pointList.querySelectorAll('li')[2];

  let sliderValue = slider.value + '%';
  sliderCounter.innerText = sliderValue;

  function handleInputChange(e) {
    let target = e.target
    if (e.target.type !== 'range') {
      target = slider
    }
    const min = target.min,
          max = target.max,
          val = target.value,
          position = (val - min) * 100 / (max - min) + '%'

    target.style.backgroundSize = position + '150%'
    sliderCounter.style.left = position;
  }

  slider.addEventListener('input', function() {
    let sliderValue = slider.value + '%';
    sliderCounter.innerText = sliderValue;

    if (slider.value >= 100 / 3) {
      addCustomClass(pointItemFirst, activeClass)
    } else if (slider.value < 100 / 3) {
      removeCustomClass(pointItemFirst, activeClass)
    }
    if (slider.value >= (100 / 3) * 2) {
      addCustomClass(pointItemSecond, activeClass)
    } else if ((slider.value < (100 / 3) * 2)) {
      removeCustomClass(pointItemSecond, activeClass)
    }
  });

  slider.addEventListener('input', handleInputChange)
})


