import { removeClassInArray, addCustomClass, removeCustomClass } from '../functions/customFunctions'
import vars from '../_vars';

import lottie from 'lottie-web';

const {overlay,
  hiddenList,
  heroImage,
  counter,
  animationIcon,
  preloadBox,
  headerTag,
  mainTag,
  footerTag,
  chartPreload
} = vars;

const arr = [headerTag, footerTag, mainTag];

function initPreload(items) {
  if(items) {
    items.map((item) => {
      const path = item.getAttribute('data-preload');
      lottie.loadAnimation({
        container: item,
        renderer: 'svg',
        loop: true,
        autoplay: true,
        path: `../lottie/${path}.json`
      });

      setTimeout(function(){
        item.parentNode.classList.add('loaded')
        item.classList.add('active');
      }, 5000);

    })
  }
}

window.onload = function () {

  initPreload(chartPreload)


  if (preloadBox) {
    window.setTimeout(function () {
      removeCustomClass(preloadBox, 'active');

      arr.forEach(function(item) {
        item ? item.style.opacity = 'initial' : '';
      })
    }, 3500);
  } else {
    arr.forEach(function(item) {
      item ? item.style.opacity = '1' : '';
    })
  }

  hiddenList ? removeCustomClass(hiddenList, 'none') : '';
  overlay ? removeCustomClass(overlay, 'none') : '';
  heroImage ? removeCustomClass(heroImage, 'none-animate') : '';

  let limit = 0;

  for (let i = 0; i < counter.length; i++) {
    setTimeout(function () {
      let x = 0;
      limit++;
      let int = setInterval(function () {
        x = x + Math.ceil(counter[i].dataset.to / 45);
        counter[i].innerText = x;
        if (x > counter[i].dataset.to) {
          counter[i].innerText = counter[i].dataset.to;
          clearInterval(int);
        }
      }, 22.5);
    }, 5000)


  }


}

if (document.querySelector('[data-btn-inner="wait"]')) {
  document.querySelector('[data-btn-inner="wait"]').addEventListener('click', function(){
    addCustomClass(animationIcon, 'active')
    lottie.loadAnimation({
      container: animationIcon,
      renderer: 'svg',
      loop: true,
      autoplay: true,
      path: `../lottie/wait.json`
    });

  });
}

if (preloadBox) {
  lottie.loadAnimation({
    container: preloadBox.querySelector('.main-preloader__image'),
    renderer: 'svg',
    loop: false,
    autoplay: true,
    path: `../lottie/main.json`
  });
}






